@use 'sass:meta';
// to utilize lighten and darken functions
$color-darkest: #16191c;
$color-green: #82be90;
$color-blue: #4c75f2;
$color-lightest: #fff;

:root {
  --color-darkest: #{meta.inspect($color-darkest)};
  --color-green: #{meta.inspect($color-green)};
  --color-blue: #{meta.inspect($color-blue)};
  --color-lightest: #{meta.inspect($color-lightest)};

  --font-text: 'Montserrat', sans-serif;
  --font-heading: 'Montserrat', sans-serif;

  --sm-blure: blur(3px);
  --md-blure: blur(6px);
  --lg-blure: blur(9px);

  --sm-shadow-dark: 0 2px 6px 0 rgba(0, 0, 0, 0.3);
  --sm-shadow-light: 0 2px 6px 0 rgba(255, 255, 255, 0.08); // the white shadow is way more noticable than the black one, hence the opacity is lower

  --base-spacing: 16px;
  --base-radius: 4px;

  --sm-light-border: 1px solid rgba(255, 255, 255, 0.3);
  --sm-dark-border: 1px solid rgba(0, 0, 0, 0.3);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  // padding: 400px;
  background-color: var(--color-lightest);
  color: var(--color-darkest);
  font-family: var(--font-text);
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  object-fit: cover;
}
