@use '../base';
@import '../mixins';
.hero {
  position: relative;
  min-height: 80vh;
  background-color: var(--color-darkest);
  color: var(--color-lightest);
  margin: spacing(0.5);
  margin-bottom: spacing(2.5);
  padding: spacing(3) spacing(4);
  border-radius: radius(4);
  display: flex;
  flex-direction: column;
  .main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    font-weight: 300;
    ul {
      display: flex;
      gap: spacing(2);
      align-items: baseline;
      .logo {
        text-transform: uppercase;
        background-image: linear-gradient(
          to right,
          var(--color-green),
          var(--color-blue)
        );
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        color: transparent;
        font-weight: 900;
        color: var(--color-green);
      }
    }
  }

  &-text {
    margin-block: auto;
    transform: translateY(spacing(-3));
  }

  &-btns {
    margin-top: spacing(2.5);
    display: flex;
    gap: spacing(1);
  }
  &-paragraph {
    font-weight: 300;
    width: 45ch;
    font-size: 1.3rem;
    line-height: 1.5;
    color: darken(base.$color-lightest, 16%);
    margin-bottom: spacing(2.5);
  }

  .scroll-arrow {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%) translateY(50%);
    aspect-ratio: 1/1;
    width: 5rem;
    font-size: 2rem;
    font-weight: 900;
    background-color: var(--color-lightest);
    // border: 1px solid var(--color-darkest);
    color: var(--color-darkest);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    i {
      animation: bounce 1s infinite alternate;
    }
    @keyframes bounce {
      0% {
        transform: translateY(0);
      }
      100% {
        transform: translateY(0.5rem);
      }
    }
  }

  .animated-circle {
    position: absolute;
    top: calc(50% - 15rem);
    right: spacing(10);
    width: 30rem;
    aspect-ratio: 1/1;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 75%;
      opacity: 0.8;
    }
    .circle {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(
        to right,
        var(--color-green),
        var(--color-blue)
      );
      // background-size: 400% 400%;

      filter: var(--lg-blure);
      opacity: 0.05;
      aspect-ratio: 1/1;
      border-radius: 50%;
      background-color: var(--color-lightest);
      animation: circle 5s infinite alternate;
    }

    // animation for circle to grow and shrink and skew and change gradient color
    @keyframes circle {
      0% {
        transform: scale(1);
      }
      100% {
        transform: scale(1.2);
      }
    }
  }
}
