@use 'base';
// button styles are intended for dark backgrounds
// they work for light backgrounds too but the shadow must be changed with the --dark-shadow modifier
// and there is a dark button style as well if needed
.btn {
  display: inline-block;
  background-color: var(--color-lightest);
  color: var(--color-darkest);
  padding: calc(var(--base-spacing) / 2) calc(var(--base-spacing) * 1.5);
  border-radius: var(--base-radius);
  border: none;
  font-size: 14px;
  cursor: pointer;
  user-select: none;
  transition: all 0.3s;
  font-weight: 400;
  &:hover,
  &:focus {
    background-color: darken(base.$color-lightest, 16%);
    box-shadow: var(--sm-shadow-light);
    transform: translateY(-1px);
  }
  &:active {
    transform: translateY(1px);
    box-shadow: none;
  }
  &--big {
    padding-block: calc(var(--base-spacing) / 1.5);
    font-size: 16px;
  }
  &--primary {
    background-color: var(--color-blue);
    color: var(--color-lightest);
    border-top: var(--sm-light-border);
    &:hover,
    &:focus {
      background-color: darken(base.$color-blue, 16%);
    }
  }
  &--secondary {
    // background-color: rgba(base.$color-green, 0.25);
    color: var(--color-green);
    border: 1px solid var(--color-green);
    background-color: transparent;
    &:hover,
    &:focus {
      background-color: rgba(base.$color-green, 0.1);
    }
  }

  &--gradient {
    background-image: linear-gradient(
      to right,
      var(--color-green),
      var(--color-blue)
    );
    font-weight: 500;
    color: var(--color-lightest);
    border-top: var(--sm-light-border);
  }

  &--dark {
    background-color: var(--color-darkest);
    color: var(--color-lightest);
    border-top: var(--sm-light-border);
    &:hover,
    &:focus {
      background-color: lighten(base.$color-darkest, 8%);
      box-shadow: var(--sm-shadow-dark);
    }
  }

  //   change shadows for special cases
  &--light-shadow {
    &:hover,
    &:focus {
      box-shadow: var(--sm-shadow-light);
    }
  }

  &--dark-shadow {
    &:hover,
    &:focus {
      box-shadow: var(--sm-shadow-dark);
    }
  }
}
